export default "import React from 'react';\n\nclass BaseContainer extends React.Component {\n  state = {\n    counter: 0,\n  };\n  onIncrease = () => {\n    this.setState((prevState) => ({\n      counter: prevState.counter + 1,\n    }));\n  };\n  onDecrease = () => {\n    this.setState((prevState) => ({\n      counter: prevState.counter - 1,\n    }));\n  };\n}\n\nexport { BaseContainer };\n"