export default "import React from 'react';\nimport {\n  FormFieldText,\n  FormFieldTextarea,\n  FormFieldSelect,\n} from './FormFields';\n\nconst professionList = [\n  { label: 'Frontend', value: 1 },\n  { label: 'Backend', value: 2 },\n  { label: 'QA', value: 3 },\n];\n\nclass FormFields extends React.Component {\n  render() {\n    return (\n      <form>\n        <FormFieldText\n          label=\"First name\"\n          name=\"firstName\"\n          error=\"Error message\"\n        />\n        <FormFieldText\n          label=\"Last name\"\n          name=\"lastName\"\n          error=\"Error message\"\n        />\n        <FormFieldTextarea label=\"Bio\" name=\"bio\" error=\"Error message\" />\n        <FormFieldSelect\n          label=\"Profession\"\n          name=\"profession\"\n          error=\"Error message\"\n          options={professionList}\n        />\n        <button type=\"submit\">Submit</button>\n      </form>\n    );\n  }\n}\n\nexport { FormFields };\n"