export default "function sum(x, y) {\n  return x + y;\n}\n\nfunction div(x, y) {\n  return x / y;\n}\n\nfunction calculate(x, y) {\n  return function(func) {\n    return func(x, y);\n  };\n}\n\nconst calc3And2 = calculate(3, 2);\ncalc3And2(sum); // 5\ncalc3And2(div); // 1.5\n\nconst calc6And3 = calculate(6, 3);\ncalc6And3(sum); // 9\ncalc6And3(div); // 2\ncalc6And3((x, y) => Math.pow(x, y)); // 216\n"