export default "import React from 'react';\nimport {\n  FieldTextarea,\n  FieldText,\n  FieldSelect,\n  FieldLayout,\n} from './FormComponents';\n\nconst FormFieldText = (props) => (\n  <FieldLayout {...props}>\n    <FieldText {...props} />\n  </FieldLayout>\n);\n\nconst FormFieldTextarea = (props) => (\n  <FieldLayout {...props}>\n    <FieldTextarea {...props} />\n  </FieldLayout>\n);\n\nconst FormFieldSelect = (props) => (\n  <FieldLayout {...props}>\n    <FieldSelect {...props} />\n  </FieldLayout>\n);\n\nexport { FormFieldText, FormFieldTextarea, FormFieldSelect };\n"