export default "import React from 'react';\nimport { FormField } from './FormField';\n\nconst professionList = [\n  { label: 'Frontend', value: 1 },\n  { label: 'Backend', value: 2 },\n  { label: 'QA', value: 3 },\n];\n\nclass FormFields extends React.Component {\n  render() {\n    return (\n      <form>\n        <FormField\n          label=\"First name\"\n          name=\"firstName\"\n          error=\"Error message\"\n          type=\"text\"\n        />\n        <FormField\n          label=\"Last name\"\n          name=\"lastName\"\n          error=\"Error message\"\n          type=\"text\"\n        />\n        <FormField\n          label=\"Bio\"\n          name=\"bio\"\n          error=\"Error message\"\n          type=\"textarea\"\n        />\n        <FormField\n          label=\"Profession\"\n          name=\"profession\"\n          error=\"Error message\"\n          type=\"select\"\n          options={professionList}\n        />\n        <button type=\"submit\">Submit</button>\n      </form>\n    );\n  }\n}\n\nexport { FormFields };\n"