export default "import React from 'react';\n\nclass FormFields extends React.Component {\n  renderField({ label, name, error, type }) {\n    return (\n      <div className=\"form-field\">\n        <label htmlFor={name}>{label}:</label>\n        {type === 'textarea' ? (\n          <textarea id={name} name={name} />\n        ) : (\n          <input id={name} name={name} type=\"text\" />\n        )}\n        <p>{error}</p>\n      </div>\n    );\n  }\n  render() {\n    return (\n      <form>\n        {this.renderField({\n          label: 'First name',\n          name: 'firstName',\n          error: 'Error message',\n          type: 'text',\n        })}\n        {this.renderField({\n          label: 'Last name',\n          name: 'lastName',\n          error: 'Error message',\n          type: 'text',\n        })}\n        {this.renderField({\n          label: 'Bio',\n          name: 'bio',\n          error: 'Error message',\n          type: 'textarea',\n        })}\n        <button type=\"submit\">Submit</button>\n      </form>\n    );\n  }\n}\n\nexport { FormFields };\n"