export default "import React from 'react';\n\nclass BaseComponent extends React.Component {\n  renderTitle() {\n    return 'Base title';\n  }\n  renderContent() {\n    return 'Base content';\n  }\n  render() {\n    return (\n      <div>\n        <h3>{this.renderTitle()}</h3>\n        <p>{this.renderContent()}</p>\n      </div>\n    );\n  }\n}\n\nexport { BaseComponent };\n"