export default "import React from 'react';\n\nconst professionList = [\n  { label: 'Frontend', value: 1 },\n  { label: 'Backend', value: 2 },\n  { label: 'QA', value: 3 },\n];\n\nclass FormFields extends React.Component {\n  renderField({ label, name, error, type, options }) {\n    let input;\n    switch (type) {\n      case 'textarea':\n        input = <textarea id={name} name={name} />;\n        break;\n      case 'select':\n        input = (\n          <select id={name} name={name}>\n            {options.map(({ label, value }, key) => (\n              <option key={key} value={value}>\n                {label}\n              </option>\n            ))}\n          </select>\n        );\n        break;\n      default:\n        input = <input id={name} name={name} type=\"text\" />;\n        break;\n    }\n    return (\n      <div className=\"form-field\">\n        <label htmlFor={name}>{label}:</label>\n        {input}\n        <p>{error}</p>\n      </div>\n    );\n  }\n  render() {\n    return (\n      <form>\n        {this.renderField({\n          label: 'First name',\n          name: 'firstName',\n          error: 'Error message',\n          type: 'text',\n        })}\n        {this.renderField({\n          label: 'Last name',\n          name: 'lastName',\n          error: 'Error message',\n          type: 'text',\n        })}\n        {this.renderField({\n          label: 'Bio',\n          name: 'bio',\n          error: 'Error message',\n          type: 'textarea',\n        })}\n        {this.renderField({\n          label: 'Profession',\n          name: 'profession',\n          error: 'Error message',\n          type: 'select',\n          options: professionList,\n        })}\n        <button type=\"submit\">Submit</button>\n      </form>\n    );\n  }\n}\n\nexport { FormFields };\n"